"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card } from "@/components/ui/card";
import Link from "next/link";
import { useAuth } from "@/context/AuthContext";
import { useRouter } from "next/navigation";

export default function LoginPage() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [userType, setUserType] = useState<"customer" | "employee" | "admin">("customer");
  const [error, setError] = useState("");
  const { login } = useAuth();
  const router = useRouter();

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError("");

    try {
      // In a real application, you would validate credentials against a backend
      // For now, we'll simulate a successful login
      login(userType);
      
      // Redirect based on role
      switch(userType) {
        case "admin":
          router.push("/admin/dashboard");
          break;
        case "employee":
          router.push("/employee/dashboard");
          break;
        case "customer":
          router.push("/dashboard");
          break;
      }
    } catch (err) {
      setError("Invalid credentials. Please try again.");
    }
  };

  return (
    <div className="min-h-screen bg-black text-white flex items-center justify-center p-4">
      <Card className="w-full max-w-md bg-zinc-900 border-0 p-8">
        <div className="text-center mb-8">
          <h1 className="text-2xl font-bold mb-2">Welcome to HPU Sports Facilities</h1>
          <p className="text-gray-400">Please login to continue</p>
        </div>

        {error && (
          <div className="bg-red-500/10 border border-red-500 text-red-500 px-4 py-2 rounded mb-6">
            {error}
          </div>
        )}

        <form onSubmit={handleSubmit} className="space-y-6">
          <div>
            <label className="block text-sm font-medium mb-2">Login as</label>
            <div className="grid grid-cols-3 gap-4 mb-4">
              <Button
                type="button"
                variant={userType === "customer" ? "default" : "outline"}
                className={userType === "customer" 
                  ? "bg-gradient-to-r from-blue-500 to-indigo-600 text-white hover:from-blue-600 hover:to-indigo-700 transition-all duration-300" 
                  : "text-white hover:bg-zinc-800 transition-all duration-300"
                }
                onClick={() => setUserType("customer")}
              >
                Customer
              </Button>
              <Button
                type="button"
                variant={userType === "employee" ? "default" : "outline"}
                className={userType === "employee" 
                  ? "bg-gradient-to-r from-blue-500 to-indigo-600 text-white hover:from-blue-600 hover:to-indigo-700 transition-all duration-300" 
                  : "text-white hover:bg-zinc-800 transition-all duration-300"
                }
                onClick={() => setUserType("employee")}
              >
                Employee
              </Button>
              <Button
                type="button"
                variant={userType === "admin" ? "default" : "outline"}
                className={userType === "admin" 
                  ? "bg-gradient-to-r from-blue-500 to-indigo-600 text-white hover:from-blue-600 hover:to-indigo-700 transition-all duration-300" 
                  : "text-white hover:bg-zinc-800 transition-all duration-300"
                }
                onClick={() => setUserType("admin")}
              >
                Admin
              </Button>
            </div>
          </div>

          <div>
            <Input
              type="email"
              placeholder="Email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              className="bg-zinc-800 border-zinc-700 text-white"
              required
            />
          </div>

          <div>
            <Input
              type="password"
              placeholder="Password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className="bg-zinc-800 border-zinc-700 text-white"
              required
            />
          </div>

          <Button 
            type="submit" 
            className="w-full bg-gradient-to-r from-blue-500 to-indigo-600 text-white hover:from-blue-600 hover:to-indigo-700 transition-all duration-300"
          >
            Login
          </Button>

          {userType === "customer" && (
            <p className="text-center text-sm text-gray-400">
              Don't have an account?{" "}
              <Link href="/auth/signup" className="text-blue-400 hover:text-blue-300 transition-all duration-300">
                Sign up
              </Link>
            </p>
          )}
        </form>
      </Card>
    </div>
  );
}
